import { Component } from 'react';
import Flip from 'react-reveal/Flip';
import Pulse from 'react-reveal/Pulse';

class Card extends Component {
    state = {
        notHidden:'',
        hidden:'hidden'
    }
    hoverOn = ()=>{
        this.setState({ hidden:'',notHidden:'hidden' });
     }
     hoverOff = ()=>{
        this.setState({ hidden:'hidden',notHidden:''  });
     }
    render() { 

            
            return ( 
                
                <div className="">
                <Flip left>
                <div className={`relative w-40 bg-center bg-cover rounded-lg shadow-2xl h-60 ${this.state.notHidden} text-right`}  style={{backgroundImage: `url(${this.props.imgUrl})`}} onMouseEnter={this.hoverOn}
                    onMouseLeave={this.hoverOff}>
                    <div className="">
                    <div className="relative h-20 bg-white rounded-t-lg">
                        <div className="absolute bottom-0 left-0 transform -skew-x-6 -rotate-12">
                        <p className={`inline px-2 text-xs font-semibold text-gray-300 capitalize ${this.props.color} rounded-lg `}>{this.props.title}</p>
                        </div>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1300 320" className="">
                        <path fill="#fff" d="M0 288L1300 32V0H0z"></path>
                    </svg>
                    </div>
                </div>
                </Flip>
                <Pulse>
                <div className={`w-40 rounded-lg h-60 ${this.props.color}  ${this.state.hidden} text-white`} onMouseEnter={this.hoverOn}
                    onMouseLeave={this.hoverOff}>
                    <div className="p-2">
                        <div className="leading-10">
                        <p className="p-3 text-xl font-semibold text-gray-200 capitalize">{this.props.title}</p>
                        </div>
                        <div className="text-xs">
                        {this.props.content}
                        </div>
                    </div>
                </div>
                </Pulse>
                </div>
             );
    }
}
 
export default Card;

