import Card from '../../components/card/card.component'
import ServicesData from './servicesData'
import Zoom from 'react-reveal/Zoom'
const Services = () => {
  let data = ServicesData()
  return (
    <div
      className="flex items-center justify-center "
      style={{ background: '#edf2f7' }}
    >
      <div className="w-full font-sans bg-white ">
        <div>
{/*  */}
          <div className="bg-blue-200 md:overflow-hidden">
            <div className="px-4 py-16">
              <div className="relative w-full text-center md:max-w-2xl md:mx-auto">
                <h1 className="mb-6 text-xl font-bold leading-tight text-blue-700 sm:text-2xl md:text-5xl">
                <Zoom left>
                <div className="p-2 mx-12 text-3xl font-bold border-b-4 border-blue-500">Services</div>
                </Zoom>
                </h1>

                <div className="absolute bottom-0 right-0 hidden w-40 h-40 -mb-48 bg-blue-300 rounded-full -mr-36 md:block"></div>
                
                <div className="absolute top-0 right-0 hidden -mr-48 bg-pink-200 rounded-full mt-44 w-28 h-28 md:block"></div>
                

              </div>
            </div>

            <svg
              className="hidden text-white bg-blue-200 fill-current md:block"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1440 320"
            >
              <path
                fillOpacity="1"
                d="M0,64L120,85.3C240,107,480,149,720,149.3C960,149,1200,107,1320,85.3L1440,64L1440,320L1320,320C1200,320,960,320,720,320C480,320,240,320,120,320L0,320Z"
              ></path>
            </svg>
          </div>
{/*  */}

          <div
            className="relative z-20 hidden max-w-4xl mx-auto bg-white shadow-lg md:block"
            style={{ marginTop: '-320px', borderRadius: '20px' }}
          >

            <div className="h-10 bg-white border-gray-100 rounded-t-lg"></div>
            <div className="flex bg-white" >
              <div className="flex-1 px-8 py-6">
                {/* Content start */}
                   <div className="flex flex-wrap p-4 justify-evenly">

                    {data.map((res)=>{
                      return <div className="m-2" key={res._id}><Card title={res.title} imgUrl={res.imgUrl} content={res.content} color={res.color} /></div>

                    })}
                    </div>
                {/* Content end */}
              </div>
            </div>
            <div
              className="absolute top-0 left-0 w-40 h-40 -mt-10 -ml-10 bg-green-300 rounded-full"
              style={{ zIndex: -1 }}
            ></div>

            <div
              className="absolute top-0 left-0 mt-12 -ml-20 bg-blue-300 rounded-full w-28 h-28"
              style={{ zIndex: -1 }}
            ></div>
          </div>

          <div className="px-4 md:hidden">
            <div
              className="relative z-20 max-w-4xl mx-auto -mt-10 bg-white shadow-lg"
              style={{ borderRadius: '20px' }}
            >
              <div className="h-10 bg-white border-gray-100 rounded-t-lg"></div>
              <div className="flex" >
                {/* Content start */}
                <div className="flex flex-wrap p-4 justify-evenly">

                {data.map((res)=>{
                  return <div className="m-2" key={res._id}><Card title={res.title} imgUrl={res.imgUrl} content={res.content} color={res.color} /></div>

                })}
                </div>
                {/* Content end */}
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  )
}

export default Services
