import DownArrow from '../down-arrow/down-arrow.component';
import Header from '../header/header.component';
import './hero.css'
import Interactive from 'react-interactive';
import { HashLink } from 'react-router-hash-link';

const Hero = () => {
    return ( 
        <div className="min-h-screen " id="hero">
        <Header />
        
                    <div className=" md:pt-12 md:mt-12 md:ml-4" >
                        <div className="flex">
                        <div className="p-2 my-10 text-2xl font-bold tracking-wider text-white md:text-6xl ">
                            Hi, my name is 
                            <div className="tracking-tight text-transparent bg-gradient-to-r from-blue-900 via-blue-700 to-red-500 bg-clip-text animate-gradient "  style={{backgroundSize: '320% 200%'}}>Mohammad Rajaul</div>
                        </div>
                        </div>
                        
                        <div className="flex p-2 text-2xl font-bold tracking-wider text-white md:text-6xl">
                            <div className=" md:mt-2">
                            A  
                            </div>
                            <div  className="relative bg-blue-900">
                                <div  className="absolute top-0 opacity-0 whitespace-nowrap left-2 md:left-8 animate-topToBottom ">
                                    <span className="p-2 text-xl rounded-full md:text-4xl bg-gradient-to-r from-blue-900 via-blue-700 to-red-500 animate-gradient"  style={{backgroundSize: '320% 200%'}}>Full stack Developer.</span> 
                                </div>
                                <div  className="absolute top-0 opacity-0 left-2 md:left-8 animate-topToBottom whitespace-nowrap" style={{animationDelay:'4s'}}>
                                    <span className="p-2 text-xl rounded-full md:text-4xl bg-gradient-to-r from-blue-900 via-blue-700 to-red-500 animate-gradient"  style={{backgroundSize: '320% 200%'}}>Front-end Developer.</span> 
                                </div>
                                <div  className="absolute top-0 opacity-0 left-2 md:left-8 animate-topToBottom whitespace-nowrap " style={{animationDelay:'8s'}}>
                                    <span className="p-2 text-xl rounded-full md:text-4xl bg-gradient-to-r from-blue-900 via-blue-700 to-red-500 animate-gradient"  style={{backgroundSize: '320% 200%'}}>Back-end Developer.</span> 
                                </div>
                                <div  className="absolute top-0 opacity-0 left-2 md:left-8 animate-topToBottom whitespace-nowrap " style={{animationDelay:'12s'}}>
                                    <span className="p-2 text-xl rounded-full md:text-4xl bg-gradient-to-r from-blue-900 via-blue-700 to-red-500 animate-gradient"  style={{backgroundSize: '320% 200%'}}>Laravel Developer.</span> 
                                </div>
                                <div  className="absolute top-0 opacity-0 left-2 md:left-8 animate-topToBottom whitespace-nowrap " style={{animationDelay:'16s'}}>
                                    <span className="p-2 text-xl rounded-full md:text-4xl bg-gradient-to-r from-blue-900 via-blue-700 to-red-500 animate-gradient"  style={{backgroundSize: '320% 200%'}}>UI Designer.</span> 
                                </div>
                            </div>
                        </div>
                        <div className="flex justify-center w-3/4 py-2 mx-2 my-12 border-2 border-blue-200 rounded-lg shadow-inner md:w-2/3 lg:w-2/5 hov-anim bg-gradient-to-tr from-blue-700 via-blue-500 to-red-500 animate-gradient"  style={{backgroundSize: '320% 200%'}}>
                            <a className="text-lg text-white whitespace-no-wrap md:text-xl" href="#about">Hire me for your project.</a>
                        </div>
                        <Interactive as={HashLink}  scroll={el => el.scrollIntoView({ behavior: 'smooth' })}  to={`#services`}>
                        <DownArrow />
                        </Interactive>
                    </div>
        </div>
     );
}
 
export default Hero;