import arrow from './arrow.png'

const DownArrow = () => {
    return ( 
        <div className="flex justify-center mt-8">
        <div className="relative">
        <img className="w-12 h-12 opacity-50 animate-pulse" src={arrow} alt="arrow" />
        <div className="absolute w-5 h-5  top-3 inset-x-3.5 animate-bounce">
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="#ccc">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
        </svg>
        </div>
        </div>
    
    </div>
     );
}
 
export default DownArrow;